section{margin-bottom:6.25rem;color:#fff;margin-top:0}.content{display:flex;flex-direction:row}.content img{width:100%;border-radius:.25rem;position:relative}.content .imageContainer{margin:1rem 1rem 0 2.5rem}.content .imageWrapper{display:inline-block;position:relative;width:16rem;height:16rem}.content .imageWrapper img{position:relative;z-index:-1}.content .imageWrapper::after{width:100%;height:100%;position:absolute;top:1rem;left:1rem;transition:all .25s cubic-bezier(0.53, 0.16, 0.62, 1.02);border:.25rem solid #d64e00;border-radius:.25rem;content:"";z-index:-2}.content .imageWrapper:hover:after{top:.5rem;left:.5rem;transition:all .25s cubic-bezier(0.53, 0.16, 0.62, 1.02)}.content p{margin-top:1rem;width:40rem}.content p ul{margin:.5rem .25rem 0;columns:2;-webkit-columns:2;-moz-columns:2;width:25rem;list-style:none}.content p ul li{width:15rem}.content p ul li::before{content:"♦";color:#d64e00;font-weight:bold;display:inline-block;width:1rem;margin-left:0}@media screen and (max-width: 75rem){.content{flex-direction:column}.content .imageContainer{display:flex;justify-content:center;margin:1rem 0 0}.content p{margin-top:1rem !important;width:100%;text-align:justify}.content ul{width:100% !important}.content .imageWrapper{margin-top:1.5rem}}@media screen and (max-width: 75rem)and (max-width: 48rem){.imageWrapper{width:13rem !important;height:13rem !important}}